<template>
  <section-container header-text="User info" class="user-info-container">
    <div class="info-field">
      Name: <span> {{ user.displayName }} </span>
    </div>
    <div class="info-field">
      Email: <span> {{ user.email }} </span>
    </div>
    <button @click="logout" class="submit-btn btn-primary">Logout</button>
  </section-container>
</template>

<script>
import SectionContainer from '@/components/SectionContainer'
import { mapActions, mapGetters } from 'vuex'

export default {
  name: 'UserProfile',
  components: { SectionContainer },
  computed: {
    ...mapGetters({
      user: "getCurrentUser"
    })
  },
  methods: {
    ...mapActions({
      logout: "logout"
    })
  }
}
</script>

<style lang="scss" scoped>
.user-info-container {
  margin-top: 35px;

  .info-field {
    margin-bottom: 20px;
    font-weight: bold;

    span {
      font-weight: normal;
    }
  }
}
</style>
